import { Color, Component, Label, Layout, Node, Prefab, Sprite, SpriteFrame, Toggle, UIOpacity, instantiate, path } from "cc";
import { ResMgr } from "../manager/ResMgr";
import { page1 } from "./page1";
import { page2 } from "./page2";
import { page3 } from "./page3";
import { page4 } from "./page4";
export const skill_quality_color = [`#ffffff`, `#8AEF4A`, `#8AE9F5`, `#F188F1`, `#F8B034`, `#F17161`, `#ff0000`, `#00ff00`, `#0000ff`]
export class pageitem extends Component {
    

    protected onLoad(): void {
      
    }
    /**
     * 刷新道具数据
     * @param data 
     */
    async SetData(data) {
        console.log(data);

        let pageName = ["page1", "page2", "page3", "page4", "page5"]
        let prefabs:string = 'prefabs/' + pageName[data];
        ResMgr.GetResources(prefabs, (prefab: Prefab) => {
            let node = instantiate(prefab);
            switch (data) {
                case 0:
                    node.addComponent(page1); 
                    break;
                case 1:
                    node.addComponent(page2); 
                    break;
                case 2:
                    node.addComponent(page3); 
                    break;
                case 3:
                    node.addComponent(page4); 
                    break;
            
                default:
                    break;
            }
            this.node.addChild(node)
        })
    }
}